import React, { useEffect, useState } from 'react';
import {
  Box,
  Container,
  Grid,
  Paper,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  Card,
  CardContent,
} from '@mui/material';
import {
  Group as UserIcon,
  AccountBalance as LoanIcon,
  AttachMoney as CashIcon,
  AccountBalanceWallet as SavingsIcon,
  Business as BankIcon,
  Assessment as LoanCountIcon,
} from '@mui/icons-material';

interface Loan {
  _id: string;
  userId: {
    fullName: string;
  };
  amount: number;
  status: string;
  createdAt: string;
}

interface DashboardStats {
  activeUsers: number;
  borrowers: number;
  cashDisbursed: number;
  cashReceived: number;
  savings: number;
  repaidLoans: number;
  otherAccounts: number;
  totalLoans: number;
}

const AdminDashboard = () => {
  const [loans, setLoans] = useState<Loan[]>([]);
  const [stats, setStats] = useState<DashboardStats>({
    activeUsers: 200,
    borrowers: 100,
    cashDisbursed: 550000,
    cashReceived: 1000000,
    savings: 450000,
    repaidLoans: 30,
    otherAccounts: 10,
    totalLoans: 50,
  });

  useEffect(() => {
    fetchLoans();
    fetchStats();
  }, []);

  const fetchLoans = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/loans/verified', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
        },
      });
      const data = await response.json();
      setLoans(data);
    } catch (error) {
      console.error('Error fetching loans:', error);
    }
  };

  const fetchStats = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/loans/statistics', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
        },
      });
      const data = await response.json();
      setStats(prevStats => ({
        ...prevStats,
        ...data,
      }));
    } catch (error) {
      console.error('Error fetching statistics:', error);
    }
  };

  const handleLoanAction = async (loanId: string, action: 'approve' | 'reject') => {
    try {
      const response = await fetch(`http://localhost:5000/api/loans/approve/${loanId}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
        },
        body: JSON.stringify({ status: action }),
      });

      if (response.ok) {
        fetchLoans();
        fetchStats();
      } else {
        alert('Failed to update loan status');
      }
    } catch (error) {
      console.error('Error updating loan:', error);
    }
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Grid container spacing={3}>
        {/* Statistics Cards */}
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <UserIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.activeUsers}</Typography>
                  <Typography color="textSecondary">ACTIVE USERS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <LoanIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.borrowers}</Typography>
                  <Typography color="textSecondary">BORROWERS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <CashIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.cashDisbursed.toLocaleString()}</Typography>
                  <Typography color="textSecondary">CASH DISBURSED</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <CashIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.cashReceived.toLocaleString()}</Typography>
                  <Typography color="textSecondary">CASH RECEIVED</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/* Second Row of Statistics */}
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <SavingsIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.savings.toLocaleString()}</Typography>
                  <Typography color="textSecondary">SAVINGS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <LoanIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.repaidLoans}</Typography>
                  <Typography color="textSecondary">REPAID LOANS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <BankIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.otherAccounts}</Typography>
                  <Typography color="textSecondary">OTHER ACCOUNTS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={3}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <LoanCountIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.totalLoans}</Typography>
                  <Typography color="textSecondary">LOANS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/* Recent Loans Table */}
        <Grid item xs={12}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" gutterBottom component="div">
              Recent Loans
            </Typography>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Customer Name</TableCell>
                    <TableCell>Amount</TableCell>
                    <TableCell>Date</TableCell>
                    <TableCell>Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {loans.map((loan) => (
                    <TableRow key={loan._id}>
                      <TableCell>{loan.userId.fullName}</TableCell>
                      <TableCell>{loan.amount.toLocaleString()}</TableCell>
                      <TableCell>
                        {new Date(loan.createdAt).toLocaleDateString()}
                      </TableCell>
                      <TableCell>
                        <Button
                          variant="contained"
                          color="success"
                          size="small"
                          onClick={() => handleLoanAction(loan._id, 'approve')}
                          sx={{ mr: 1 }}
                        >
                          Approve
                        </Button>
                        <Button
                          variant="contained"
                          color="error"
                          size="small"
                          onClick={() => handleLoanAction(loan._id, 'reject')}
                        >
                          Reject
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default AdminDashboard; 