import React, { useEffect, useState } from 'react';
import {
  Box,
  Container,
  Grid,
  Paper,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  Card,
  CardContent,
} from '@mui/material';
import {
  AccountBalance as LoanIcon,
  Group as BorrowerIcon,
  AttachMoney as CashIcon,
  Savings as SavingsIcon,
} from '@mui/icons-material';

interface Loan {
  _id: string;
  userId: {
    fullName: string;
    email: string;
  };
  amount: number;
  status: string;
  createdAt: string;
}

interface Stats {
  loans: number;
  borrowers: number;
  cashDisbursed: number;
  savings: number;
  repaidLoans: number;
  cashReceived: number;
}

const VerifierDashboard = () => {
  const [loans, setLoans] = useState<Loan[]>([]);
  const [stats, setStats] = useState<Stats>({
    loans: 50,
    borrowers: 100,
    cashDisbursed: 550000,
    savings: 450000,
    repaidLoans: 30,
    cashReceived: 1000000,
  });

  useEffect(() => {
    fetchLoans();
  }, []);

  const fetchLoans = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/loans/pending', {
        headers: {
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
        },
      });
      const data = await response.json();
      setLoans(data);
    } catch (error) {
      console.error('Error fetching loans:', error);
    }
  };

  const handleVerification = async (loanId: string, action: 'approve' | 'reject') => {
    try {
      const response = await fetch(`http://localhost:5000/api/loans/verify/${loanId}`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${localStorage.getItem('token')}`,
        },
        body: JSON.stringify({ status: action }),
      });

      if (response.ok) {
        fetchLoans();
      } else {
        alert('Failed to update loan status');
      }
    } catch (error) {
      console.error('Error updating loan:', error);
    }
  };

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Grid container spacing={3}>
        {/* Statistics Cards */}
        <Grid item xs={12} sm={6} md={4}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <LoanIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.loans}</Typography>
                  <Typography color="textSecondary">LOANS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <BorrowerIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.borrowers}</Typography>
                  <Typography color="textSecondary">BORROWERS</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Card>
            <CardContent>
              <Box display="flex" alignItems="center">
                <CashIcon sx={{ fontSize: 40, color: '#006400', mr: 2 }} />
                <Box>
                  <Typography variant="h4">{stats.cashDisbursed.toLocaleString()}</Typography>
                  <Typography color="textSecondary">CASH DISBURSED</Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        {/* Applied Loans Table */}
        <Grid item xs={12}>
          <Paper sx={{ p: 2 }}>
            <Typography variant="h6" gutterBottom>
              Applied Loans
            </Typography>
            <TableContainer>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Customer Name</TableCell>
                    <TableCell>Exception Name</TableCell>
                    <TableCell>Date</TableCell>
                    <TableCell>Actions</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {loans.map((loan) => (
                    <TableRow key={loan._id}>
                      <TableCell>{loan.userId.fullName}</TableCell>
                      <TableCell>{loan.amount}</TableCell>
                      <TableCell>
                        {new Date(loan.createdAt).toLocaleDateString()}
                      </TableCell>
                      <TableCell>
                        <Button
                          variant="contained"
                          color="success"
                          size="small"
                          onClick={() => handleVerification(loan._id, 'approve')}
                          sx={{ mr: 1 }}
                        >
                          Approve
                        </Button>
                        <Button
                          variant="contained"
                          color="error"
                          size="small"
                          onClick={() => handleVerification(loan._id, 'reject')}
                        >
                          Reject
                        </Button>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default VerifierDashboard; 