import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';

import { theme } from './styles/theme';
import Layout from './components/layout/Layout';
import Login from './components/auth/Login';
import LoanApplication from './components/user/LoanApplication';
import VerifierDashboard from './components/verifier/VerifierDashboard';
import AdminDashboard from './components/admin/AdminDashboard';

const PrivateRoute = ({ children, allowedRoles }: { children: JSX.Element, allowedRoles: string[] }) => {
  const token = localStorage.getItem('token');
  const userRole = localStorage.getItem('userRole');

  if (!token) {
    return <Navigate to="/login" />;
  }

  if (allowedRoles.length && !allowedRoles.includes(userRole || '')) {
    return <Navigate to="/" />;
  }

  return <Layout>{children}</Layout>;
};

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <Routes>
          <Route path="/login" element={<Login />} />
          
          <Route
            path="/"
            element={
              <PrivateRoute allowedRoles={['user']}>
                <LoanApplication />
              </PrivateRoute>
            }
          />
          
          <Route
            path="/verifier/dashboard"
            element={
              <PrivateRoute allowedRoles={['verifier']}>
                <VerifierDashboard />
              </PrivateRoute>
            }
          />
          
          <Route
            path="/admin/dashboard"
            element={
              <PrivateRoute allowedRoles={['admin']}>
                <AdminDashboard />
              </PrivateRoute>
            }
          />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App;
